.button:active {
    transform: scale(0.8);
}
.extras {
    animation: extras 600ms;
}

@keyframes extras {
    from {transform: translateY(-5rem);opacity: .4;}
    to {transform: translateY(0);opacity: 1;}
}

.redeem::before {
    content: '';
    border-style: solid;
    border-width: .8rem;
    border-color: transparent rgb(226 232 240) rgb(226 232 240) transparent;
    position: absolute;
    top: -.9rem;
    right: 3rem;
}

.redees::before {
    content: '';
    border-style: solid;
    border-width: .8rem;
    border-color: transparent rgb(30 41 59) rgb(30 41 59) transparent;
    position: absolute;
    top: -.9rem;
    right: 3rem;
}
.loading {
    width: 1rem;
    height: 1rem;
    border-top: 0.15rem solid white;
    border-right: 0.15rem solid white;
    border-bottom: 0.15rem solid white;
    border-left: 0.15rem solid transparent;
    border-radius: 50%;
    animation: loading 1s linear infinite;
}
@keyframes loading {
    to {
        transform: rotate(360deg);
    }
}
@media screen and (min-width: 768px) {
    .midh {
        height: 27rem;
        transform: translateY(-1rem);
    }
}